<template>
  <div>
   <h1>Bonjour je suis un sujet</h1>
    <p> Lorem ipsum dolor sit amet, consectetur adipisicing elit. Non molestias asperiores ad laboriosam est atque magni neque. Eligendi quia perferendis, ullam minima laudantium eius hic quasi, exercitationem omnis, soluta similique.</p>
    <button @click='switchToCreateComment()' v-if="mode ==='display'">Ecrire un commentaire</button>
    <label v-if="mode === 'create'" for="comment">Votre commentaire</label>
    <input v-if="mode === 'create'" type="text" name="comment">
    <button @click='switchToDisplay()' v-if="mode === 'create'">Envoyer le commentaire</button>
    <button @click='switchToDisplay()' v-if="mode === 'create'" >Annuler</button>  
  </div>
</template>

<script>
export default {
  name: 'MultimediaMessage',
  data: function () {
    return {
    mode: 'display'
    }
  },
  methods: {
    switchToCreateComment: function () {
      this.mode= 'create'
    },
    switchToDisplay: function () {
      this.mode= 'display'
    },
  },
}
</script>
