<template> 
<div id='signup'>
  <h1>Signup</h1>
  <form>
  <input v-model="email" type="text" name="email" placeholder="Email">

  <input v-model="prenom" type="text" name="prenom" placeholder="Prénom">

  <input v-model="nom" type="text" name="nom" placeholder="Nom">
  <input v-model="password" type="password" name="password" placeholder="Mot de passe">
  </form>
    <button @click="createAccount()">S'inscrire</button>

  </div>
</template>

<script>
export default {
  name: 'Signup',
  data: function () {
    return {
      email: '',
      prenom: '',
      nom: '',
      password: '',
    }
  },
  methods: {
    createAccount: function () {
      this.$store.dispatch('createAccount', {
          email: this.email,
          prenom: this.prenom,
          nom: this.nom,
          password: this.password,
      })
    },
  }
}
</script>
